import React, { useState, useContext, useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { Alert } from "reactstrap";
import "../../css/auth.css";
import { AuthContext } from "../../context/authContext";

function Login() {
  const { login, user } = useContext(AuthContext);

  const navigate = useNavigate();

  const [form, setForm] = useState({
    username: "",
    password: "",
  });

  const [errorMessage, setErrorMessage] = useState("");
  const [successMessage, setSuccessMessage] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (user) navigate("/check-document");
  });

  const loginUser = async (e) => {
    try {
      e.preventDefault();
      setIsLoading(false);

      setErrorMessage("");
      setSuccessMessage("");

      if (Object.values(form).every((value) => value !== "")) {
        setIsLoading(true);

        const response = await axios.post(
          `${process.env.REACT_APP_API_URL}/api/v1/users/login`,
          form
        );

        await login(response.data.data);
        setSuccessMessage(`Berhasil Login`);

        if (!localStorage.getItem("is_review_app")) {
          localStorage.setItem("is_review_app", JSON.stringify({ value: 0 }));
        }

        setForm({
          username: "",
          password: "",
        });

        navigate("/check-document");
      } else {
        setErrorMessage("Harap isi semua form");
      }
    } catch (error) {
      setErrorMessage(error.response.data.message ?? error.message);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="container">
      <div className="Auth-form-container">
        <form className="Auth-form" onSubmit={loginUser}>
          <div className="Auth-form-content">
            <h3 className="Auth-form-title">Login</h3>
            {successMessage ? (
              <Alert color="success">{successMessage}</Alert>
            ) : (
              <div />
            )}
            {errorMessage ? (
              <Alert color="danger">{errorMessage}</Alert>
            ) : (
              <div />
            )}

            <div className="form-group mt-3">
              <label>Username</label>
              <input
                type="text"
                className="form-control mt-1"
                placeholder="e.g user1234"
                onClick={() => setErrorMessage("")}
                value={form.username}
                onChange={(e) =>
                  setForm({
                    ...form,
                    username: e.target.value,
                  })
                }
                minLength={7}
                required
              />
            </div>
            <div className="form-group mt-3">
              <label>Password</label>
              <input
                type="password"
                className="form-control mt-1"
                placeholder="Password"
                onClick={() => setErrorMessage("")}
                value={form.password}
                onChange={(e) =>
                  setForm({
                    ...form,
                    password: e.target.value,
                  })
                }
                minLength={7}
                required
              />
            </div>
            <div className="d-grid gap-2 mt-3">
              <button
                disabled={isLoading}
                type="submit"
                className="btn btn-primary"
              >
                {isLoading ? "Loading..." : "Login"}
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}

export default Login;
